*{
    padding: 0;
    margin: 0;
}
body{
    background-image: url("../../public/images/backbround.png");
    background-size: cover;
    background-clip: content-box;
    background-repeat: no-repeat;
    background-position: center;
    height: 100vh;
    margin-left: 80px;
    margin-right: 80px;
}
.container{
    height: 100vh;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(12,1fr);
    grid-template-rows: repeat(7,1fr) ;
}

li{
    color: #fff;
    list-style: none;
    font-size: 20px;
}
.navbar{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    gap: 180px;
}
.navbar .logo{
    width: 70px;
    cursor: pointer;
}
.nav-container{
    grid-column: 1/13;
    grid-row: 1/2;
}
.introduct{
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.introduct .myname{
    font-size: 50px;
}
.introduct .context-info{
    font-size: 20px;
    line-height: 40px;
}
.intro-container{
    grid-column: 1/8;
    grid-row: 2/7;
    color: #fff;
    display: flex;
    flex-direction: column;
    gap: 60px;
}
.greating{
    font-size: 100px;
}
.btn{
    width: 250px;
    height: 80px;
    outline: none;
    border: none;
    border-radius: 10px;
    font-size: 25px;
    cursor: pointer;
}
.download-cv{
    background-color: #7B26EB;
    color: #fff;
}
.contact-me{
    background-color: #ffffff;
    color: #7B26EB;
}
.btn-container{
    display: flex;
    flex-direction: row;
    gap:140px;
}